.nav {
  display: flex;
  justify-content: space-between;

  &__list {
    display: flex;
    list-style: none;
  }

  &__item {
    margin-right: 20px;
    text-transform: uppercase;
    color: #89939a;
    letter-spacing: 0.04em;
    font-size: 12px;
    font-weight: 700;
  }

  .isActive {
    color: #313237;
  }

  &__link {
    text-decoration: none;
  }

}
